











































































































































import Vue from 'vue';

import { minimizedToPersonData, PersonData, MinimizedPerson } from '@/interfaces/ipeople';
import { minimizedToACData, ConfirmationMessage, TOKEN, calculateBonus } from '@/interfaces/iac';
import AppreciationCard from '../../data/ac';

import { peopleModule } from '@/store/modules/people';
import { acModule } from '@/store/modules/acs';
import QRReader from '@/components/QRReader';
import Person from '../../data/person';
import PersonTile from '@/components/PersonTile.vue';
import iconConfig from '@/store/misc/icons';

export default Vue.extend({
  name: 'SendConfirmation',
  props: {
    ac: Object as () => AppreciationCard,
    active: Boolean,
    updatecb: Function
  },
  components: {
    PersonTile
  },
  data: function() {
    return {
      qrReader: {} as QRReader,
      resultText: "" as string,
      loading: false as boolean,
      showError: false as boolean,
      errorMessage: "" as string,
      showUpdatedContact: false as boolean,
      showNewContact: false as boolean,
      localReceiver: undefined as Person | undefined,
      personData: undefined as PersonData | undefined,
      incomingCM: {} as ConfirmationMessage,
      iconConfig
    }
  },
  methods: {
    handleError(message: string): void {
      this.errorMessage = message;
      this.showError = true;
    },
    handleNewContact(): void {
      this.showNewContact = true;
    },
    handleUpdatedContact(): void {
      this.showUpdatedContact = true;
    },
    parseResult(qrData: string): void {
      if (this.loading) 
        return;
      
      this.loading = true;

      let result: any;
      try {
        result = JSON.parse(qrData);
      }
      catch(err) {
        return this.handleError("Invalid data structure in QR Code.");
      }

      if (!this.checkResultValidity(result)) 
        return this.handleError("That is not a valid confirmation message.");

      this.localReceiver = peopleModule.personByEmail(result.e);

      if (result.p) {
        if (!this.checkPersonValidity(result.p)) 
          return this.handleError("Incorrect contact details in package.");

        const mp = result.p as MinimizedPerson;
        mp.e = result.e;
        this.personData = minimizedToPersonData(mp);
        this.incomingCM = result as ConfirmationMessage;
        if (this.ac.receiver.isNewContact) 
          this.handleNewContact();
        else {
          if (result.e !== this.ac.receiver.email) 
            return this.handleError("This confirmation is coming from a different person than you intended to send it to.");
          if (this.localReceiver === undefined) 
            return this.handleError("Unexpected error occurred, Appreciation Card might be corrupt.");
          if (this.localReceiver.isEqual(this.personData))
            this.confirmAC(result);
          else
            this.handleUpdatedContact();
        }
      } 
      else {
        if (this.ac.receiver.isNewContact) 
            return this.handleError("No contact details found, although requested. Ask the other contact to include their details.");

        if (result.e !== this.ac.receiver.email) 
           return this.handleError("This confirmation is coming from a different person than you intended to send it to.");
           
        if (result.d !== this.ac.dateCreated) 
          return this.handleError("This confirmation is not matching the the outgoing appreciation.");

        this.confirmAC(result);
      }
    },
    confirmAC(cm: ConfirmationMessage): void {
      const acd = this.ac.toACData();
      acd.dateSent = cm.n;
      acd.state = 2;
      if (acModule.canObtainRewardToken(acd)) {
        acd.token = TOKEN.REWARD;
        const streak: number = acModule.canStreak;
        if (streak >= 0) {
          acd.streak = streak + 1;
          acd.bonus = calculateBonus(streak);
        }
      }
      if (this.ac.receiver.isNewContact) acd.receiver = cm.e;
        acModule.updateAC(acd)
          .then(ac => this.updatecb(ac));
    },
    checkResultValidity(jsonResult: any): boolean {
      return (jsonResult.e
        && jsonResult.d
        && jsonResult.n
      ) ? true : false;
    },
    checkPersonValidity(jsonResult: any): boolean {
      return (jsonResult.n) ? true : false;
    },
    dismissDialog() {
      this.loading = false;
      this.showError = false;
      this.showNewContact = false;
      this.showUpdatedContact = false;
      this.qrReader.start();
    },
    addNewContact() {
      this.showNewContact = false;
      //TODO: handle undefined
      if (!this.personData) return;
      peopleModule.addPerson(this.personData)
        .then(() => this.confirmAC(this.incomingCM));
    },
    updatedContact() {
      this.showUpdatedContact = false;
      //TODO: handle undefined
      if (!this.personData) return;
      peopleModule.updatePerson(this.personData)
        .then(() => this.confirmAC(this.incomingCM));
    },
    noUpdatedContact() {
      this.showUpdatedContact = false;
      this.confirmAC(this.incomingCM);
    },
  },
  computed: {
    name(): string {
      return this.ac ? this.ac.receiver.name : "";
    },
    incomingPerson(): Person | undefined {
      return this.personData ? new Person(this.personData) : undefined;
    }
  },
  watch: {
    active(newActive: boolean): void {
      if (newActive) 
        this.qrReader.start();
      else
        this.qrReader.stop();
    }
  },
  mounted: function() {
    this.qrReader = new QRReader(this.$refs.canvas as HTMLCanvasElement);
    this.qrReader.on('qrReceived', this.parseResult);
    this.qrReader.start();
  },
  beforeDestroy: function() {
    this.qrReader.stop();
  }
});
