


































































import Vue from 'vue';
import Card from '@/components/Card.vue'
import AppreciationCard from '../../data/ac';

import { acModule } from '@/store/modules/acs';

export default Vue.extend({
  name: 'Saved',
  props: {
    updatecb: Function
  },
  components: {
    Card
  },
  data() {
    return {
      loading: false as boolean
    }
  },
  methods: {
    sendCard(ac: AppreciationCard): void {
      this.updatecb(ac);
    },
    deleteCard(ac: AppreciationCard): void {
      if (this.loading) return;
      this.loading = true;
      acModule.deleteAC(ac.id)
        .then(() => this.loading = false);
    }
  },
  computed: {
    pendingACs(): Array<AppreciationCard> {
      const acs: Array<AppreciationCard> = acModule.pendingACs;
      return [...acs].sort((a, b) => b.dateCreated - a.dateCreated);
    }
  }
});
