



















































import Vue from 'vue';
import AppreciationCard from '../../data/ac';
import { TOKEN } from '../../interfaces/iac';
import iconConfig from '@/store/misc/icons';
import { acModule } from '@/store/modules/acs';

export default Vue.extend({
  name: 'SendSummary',
  data() {
    return {
      iconConfig,
      acModule
    }
  },
  props: {
    ac: Object as () => AppreciationCard
  },
  computed: {
    receiverName(): string {
      return this.ac && this.ac.receiver
        ? this.ac.receiver.name
        : "";
    },
    tokenReceived(): boolean {
      return this.ac ? this.ac.token === TOKEN.REWARD : false;
    },
    bonusReceived(): number {
      return this.ac ? this.ac.bonus : 0;
    }
  }
});
