














































import Vue from 'vue';
import QRGenerator, { ERROR_CORRECTION } from '@/components/QRGenerator.vue'
import AppreciationCard from '../../data/ac';
import iconConfig from '@/store/misc/icons';

export default Vue.extend({
  name: 'SendQR',
  components: {
    QRGenerator
  },
  props: {
    ac: Object as () => AppreciationCard,
    updatecb: Function
  },
  data: function() {
    return {
      minimizedAC: "" as string,
      checkbox: false as boolean,
      currentCorrection: ERROR_CORRECTION.MEDIUM as ERROR_CORRECTION,
      errorCorrection: ERROR_CORRECTION,
      iconConfig
    }
  },
  methods: {
    generateMessage() {
      this.updatecb(this.checkbox);
      this.minimizedAC = this.ac
        ? this.ac.receiver.isNewContact || this.checkbox
            ? JSON.stringify(this.ac.minimize(true))
            : JSON.stringify(this.ac.minimize(false))
        : "";
    }
  },
  computed: {
    name(): string {
      return this.ac ? this.ac.receiver.name : "";
    },
    concernsNewContact(): boolean {
      if (!this.ac || !this.ac.receiver) return false;
      return this.ac.receiver.isNewContact;
    }
  },
  mounted: function() {
    this.generateMessage();
    if (this.concernsNewContact) this.checkbox = true;
  },
  watch: {
    ac(): void {
      this.generateMessage();
    },
    checkbox(): void {
      this.generateMessage();
    },
    concernsNewContact(newBool: boolean): void {
      if (newBool) this.checkbox = true;
    }
  }
});
