



















































































































import Vue from 'vue';

import SavedList from '@/components/send/SavedList.vue';
import SendQR from '@/components/send/SendQR.vue';
import SendConfirmation from '@/components/send/SendConfirmation.vue';
import SendSummary from '@/components/send/SendSummary.vue';
import AppreciationCard from '../data/ac';
import { Route } from 'vue-router';

export default Vue.extend({
  name: 'Send',
  data() {
    return {
      step: 0 as number,
      activeAC: undefined as AppreciationCard | undefined,
      loading: false as boolean,
      sheet: false as boolean,
      manualRequest: false as boolean
    }
  },
  components: {
    SendQR,
    SavedList,
    SendConfirmation,
    SendSummary
  },
  methods: {
    updateAC(ac: AppreciationCard): void {
      this.activeAC = ac;
      this.step = 2;
    },
    updateManualRequest(bool: boolean): void {
      this.manualRequest = bool;
    },
    acConfirmed(ac: AppreciationCard): void {
      this.activeAC = ac;
      this.step = 4;
    },
    changeView(i: number): void {
      if (this.step === 2 && i === -1) this.sheet = true;
      else if (this.step === 1 && i === -1) this.$router.back();
      else this.step += i;
    },
    cancelSend(): void {
      this.sheet = false;
      this.activeAC = undefined;
      this.step = 1;
    },
  },
  computed: {
    stepTitle(): string {
      switch (this.step) {
        case 1: return 'Pick a card to send'
        case 2: return 'Let other person scan'
        case 3: return 'Scan QR confirmation'
        default: return 'Transaction summary'
      }
    },
    progressValue(): number {
      return (this.step - 1) * (100 / 3);
    },
  },
  beforeRouteLeave(to: Route, from: Route, next: CallableFunction) {
    if (this.step < 2) next();
    if (this.step > 3) next();
    else if (to.path === "/") {
      if (this.step === 2) this.sheet = true;
      else this.step--;
      next(false);
    } else {
      next();
    }
  }
});
